import React, { Suspense, lazy } from 'react';
const Time = lazy(() => import( "../../Elements/Time/Time"));
const Network = lazy(() => import( "../../Elements/Network/Network"));
const Author = lazy(() => import( "../../Elements/Author/Author"));
const Seprator = lazy(() => import( "../../Elements/Seprator/Seprator"));
const AuthorDetails = ({ Post, CardStyle }) => {
  const { author, networkId } = Post
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (CardStyle.author.status && !author.isInstaUser) ? true : false;
  const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !author.isInstaUser) ? true : false;
  const networkIcon = (networkId !== null && networkId !== '') ? true : false
  return <div className="tb_hc_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    {postAuthor ? <Suspense fallback={<></>}><Author network={Post.network} author={author} authorClass={'tb_hc_author_profile'} size={46}/></Suspense> : ''}
    <div className="tb_hc_author_info">
      {postAuthor ? <div className={`tb_hc_authorname tb-cTBfont-${CardStyle.author.variant}`}>{author.name}</div> : ''}
      {<div className="tb_hc_post_info">
        {postAuthor ? <div className="tb_hc_username">{username}</div> : ''}
        {authorSeprator ? <Suspense fallback={<></>}><Seprator sepClass={`tb_hc_seprator`} /></Suspense> : ''}
        {CardStyle.timeStatus ? <Suspense fallback={<></>}>
        <Time createdAt={Post.createdAt} timeClass={`tb_hc_time tb-cTBfont-${CardStyle.author.variant}`} /></Suspense> : ''}
      </div>}
    </div>
    {networkIcon ?
      <div className="tb_hc_social_">
        <Suspense fallback={<></>}>
        <Network networkClass={'tb_hc_social_ico'} networkId={networkId} iconStyle={CardStyle.icon} /></Suspense>  
      </div>
      : null}
  </div>
}
export default AuthorDetails