  import React from "react";
  import { isTagbox, isTagShop } from "../../../../actions/api";
  import Video from "../../Elements/Media/Video";
  import Image from "../../Elements/Media/Image";
  import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
  const Media = ({ Post, ThemeInfo, ThemeStyle, CardStyle, index, inView }) => {
    const autoPlay = ([3,5].includes(Post.type)) && ThemeStyle.autoPlay == 1 ? true : false;
    const size = ThemeInfo.productType == 9 || isTagbox ? CardStyle.style.ratio ? CardStyle.style.ratio : 100 : 100;
    const mediaIcon = Post.hotspot ? false : true;
    return (
      <div className="tb_hc_media_wrap">
        {mediaIcon ? (
          <MediaIcon Post={Post} IconClass="tb_hc_media_icon" show={mediaIcon} isCenter={false} hideVideo={autoPlay}/>
        ) : null}
        {autoPlay ? (
          <Video VideoClass="tb_hc_video" Post={Post} size={size} isCover={true} controls={false} autoPlay={autoPlay} muted={true} observer={false} index={index} inView={inView} />
        ) : (
          <Image ImageClass="tb_hc_image" Post={Post} size={size} hotspot={true} blurBG={isTagShop} observer={false} index={index} inView={inView} />
        )}
      </div>
    );
  };
  export default Media;
