import React, { PureComponent } from "react";
import "./scss/rating.scss";

const NetworkStar = ({ networkId }) => {
    const networkStyles = {
        3: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#1b74e4' },
        19: { className: 'tb_rating_ico__ tb__icon tb-yelp-star', color: '#e00707' },
        23: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#ff385c' },
        28: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#F8B90C' },
        29: { className: 'tb_rating_ico__ tb__icon tb-default-star', color: '#613983' },
        33: { className: 'tb_rating_ico__ tb__icon tb-trustpilot', color: '#000' },
        35: { className: 'tb_rating_ico__ tb__icon tb-star-fill', color: '#34E0A1' },
    };

    const { className, color } = networkStyles[networkId] || {
        className: 'tb_rating_ico__ tb__icon tb-default-star',
        color: '#F8B90C',
    };

    return <div className={className} style={{ color }} tb-network={networkId}><div></div></div>;
};

const ThemeStar = ({ ThemeID, networkId }) => {
    const themeStyles = {
        59: { className: 'tb_rating_ico__ tb__icon tb-star-fill', color: '#34E0A1' },
    };

    const { className, color } = themeStyles[ThemeID] || {
        className: 'tb_rating_ico__ tb__icon tb-default-star',
        color: '#F8B90C',
    };

    return <div className={className} style={{ color }} tb-network={networkId}></div>;
};
export default class Rating extends PureComponent {
    render() {
        const { rating, networkId, ThemeID } = this.props;
        return (
            <div className="tb__rating__" aria-hidden>
                {
                    rating ? [...Array(rating)].map((el, index) => ThemeID ? <ThemeStar key={index} ThemeID={ThemeID} networkId={networkId} /> : <NetworkStar key={index} networkId={networkId} /> ) : null
                }
            </div>
        );
    }
}
