import React, { PureComponent } from "react";
import { PostTrimContent  } from '../../../../utils'
import { isTagShop } from "../../../../actions/api";
import Media from "./Media";
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";
import SocialAction from "../../Elements/SocialAction/SocialAction";
import AuthorDetails from "./AuthorDetails";
import PostContent from "../../Elements/PostContent/PostContent";
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import InViewWrapper from '../../Elements/InViewWrapper';


const trim_content_length = 140;
export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      isExpanded: false,
      hiddenHeight: 0,
      height: 0
    };
    this.toggleExpand = this.toggleExpand.bind(this);
  }

  contentRef = ref => {
    this.setState({
      height: ref
    })

  }

  contentRefHidden = ref => {
    this.setState({
      hiddenHeight: ref
    })
  }

  toggleExpand = (event) => {
    event.stopPropagation()
    this.setState({
      isExpanded: !this.state.isExpanded
    })
  }


  render() {
    const { Post, CardStyle, index, ThemeInfo, onClickToCTA, ThemeStyle } = this.props;
    const mediaType = Post.type !== 1 ? true : false;
    const rating = Post.rating && (Post.rating > 0) ? true : false;
    const contentClass = (Post.rating > 0) ? 'tb_hc_rating_content' : 'tb_hc_content';
    const noMediaClass = (Post.type === 1) ? 'tb_hc_text_post' : '';
    const textDecorate = (Post.type === 1) && String(Post.content).length > 0 && (CardStyle.style.textDecoration) ? 'tb_hc_text_decoration' : '';
    const ctaActive = Post.cta.status ? true : false;
    const postTextContent =  CardStyle.content.trimcontent ? PostTrimContent(Post.content.text, trim_content_length) : Post.content.text

    return <InViewWrapper>
        {({ ref, inView }) => (
          <div id={`tb_hc_id-${Post.id}`} className="tb_hc_post_wrapper" ref={ref} style={{ padding: CardStyle.style.padding / 2 }} role="article" aria-label={`Post ${parseInt(index) + 1},${postTextContent}`}>
            <div className="tb_hc_post_in"  role="button" aria-label="Popup" tabIndex={0}>
                {Post.share.status ? <ShareOnHover share={Post.share} shareClass={"tb_hc_share_container"} Post={Post} ThemeInfo={ThemeInfo} />: ''}
                {mediaType ? <Media Post={Post} CardStyle={CardStyle} ThemeInfo={ThemeInfo} ThemeStyle={ThemeStyle} index={index} inView={inView}/> 
                : null}
              <div className={`tb_hc_contant_wrapper ${noMediaClass}`}>
                <AuthorDetails Post={Post} CardStyle={CardStyle} />
                {ctaActive ? <div className="tb_hc_post_cta" onClick={(e) => { e.stopPropagation() }}>
                  <CTAButton ctaClass={"tb_hc_post_cta_btn"} Post={Post} onClickToCTA={onClickToCTA} />
                </div>
                : ''} 
                {rating ? <div className={`tb_hc_rating__`}> 
                <Rating rating={Post.rating} networkId={Post.networkId} ThemeID={ThemeInfo.id} />
                </div> : null}
                {(CardStyle.content.status) || Post.type == 1 ?
                  <div className={`tb_hc_content_wrapper__`}>
                      <PostContent contentClass={`${contentClass}${textDecorate}`} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={Post.content.title} /> 
                  </div>
                  : null}
              </div> 
              {isTagShop  ? (CardStyle.actionStatus ?
              <SocialAction Post={Post} CardStyle={CardStyle} actionClass={`tb_hc_social_action__`} />
            : null) : ''}
            </div>
          </div>
        )}
      </InViewWrapper>
  }
}
